import React from 'react';
import Card from './Card.jsx'

export default function Cards(props) {
                              // cities => {data} => app, ya tiene el modelo de como armar cities
  return    (     
  props.cities.map( e  => 
   <div>
    <Card
          max={e.main.temp_max}
          min={e.main.temp_min}
          name={e.name}
          img={e.weather[0].icon}
          onClose={() => alert(e.name)}
          key = {e.id}
        />

</div>))
};